import { useState, useEffect, useRef } from 'react';
import { ESP32_HOSTNAMES, ESP32_WEBSOCKET_PORT } from '../config';

export const useCaseStudiesWebSocket = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [statusMessage, setStatusMessage] = useState('Connecting to haptic device...');
  const wsRef = useRef(null);
  const hostIndex = useRef(0);

  useEffect(() => {
    const connect = () => {
      const wsUrl = `ws://${ESP32_HOSTNAMES[hostIndex.current]}:${ESP32_WEBSOCKET_PORT}`;
      wsRef.current = new WebSocket(wsUrl);
      setStatusMessage(`Connecting to ${wsUrl}...`);

      wsRef.current.onopen = () => {
        setIsConnected(true);
        setStatusMessage('Connected to haptic device');
      };

      wsRef.current.onclose = () => {
        setIsConnected(false);
        if (wsRef.current.CLOSED_BY_USER) {
            setStatusMessage('Disconnected.');
        } else {
            setStatusMessage('Disconnected. Retrying...');
            hostIndex.current = (hostIndex.current + 1) % ESP32_HOSTNAMES.length;
            setTimeout(connect, 3000); // Retry after 3 seconds
        }
      };

      wsRef.current.onerror = (err) => {
        console.error('WebSocket error:', err);
        wsRef.current.close(); // This will trigger onclose and the reconnect logic
      };
    };

    connect();

    return () => {
      if (wsRef.current) {
        wsRef.current.CLOSED_BY_USER = true;
        wsRef.current.close();
      }
    };
  }, []);

  const sendTremorData = (caseId, time, torque) => {
    if (wsRef.current && wsRef.current.readyState === WebSocket.OPEN) {
      const message = {
        type: 'tremor_data',
        case_id: caseId,
        time: Array.from(time), // Convert TypedArray to regular array
        torque: Array.from(torque),
      };
      wsRef.current.send(JSON.stringify(message));
      setStatusMessage(`Simulating: ${caseId}`);
    } else {
      console.warn('WebSocket not connected');
      setStatusMessage('Haptic device not connected');
    }
  };

  const stopTremor = () => {
    if (wsRef.current && wsRef.current.readyState === WebSocket.OPEN) {
      wsRef.current.send(JSON.stringify({ type: 'stop_tremor' }));
      setStatusMessage('Ready');
    }
  };

  return { isConnected, statusMessage, sendTremorData, stopTremor };
}; 