import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useLocation } from 'react-router-dom';
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  Container, 
  Box, 
  Tabs, 
  Tab,
  CssBaseline,
  ThemeProvider,
  createTheme
} from '@mui/material';
import LiveMonitor from './components/LiveMonitor';
import ParameterControls from './components/ParameterControls';
import CaseStudies from './components/CaseStudies';
import CaseStudiesConfig from './components/CaseStudiesConfig';

const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

function NavigationTabs() {
  const location = useLocation();
  const [value, setValue] = useState(() => {
    switch (location.pathname) {
      case '/':
        return 0;
      case '/case-studies':
        return 1;
      case '/case-studies-config':
        return 2;
      default:
        return 0;
    }
  });

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Tabs value={value} onChange={handleChange} aria-label="navigation tabs">
      <Tab label="Live Monitor" component={Link} to="/" />
      <Tab label="Case Studies" component={Link} to="/case-studies" />
      <Tab label="Configuration" component={Link} to="/case-studies-config" />
    </Tabs>
  );
}

function App() {
  const [parameters, setParameters] = useState({
    G: 1.0,
    Kp: 1.0,
    Kd: 0.2,
    alpha: 1.0
  });

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Box sx={{ flexGrow: 1 }}>
          <AppBar position="static">
            <Toolbar>
              <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                Engineering Platform
              </Typography>
              <NavigationTabs />
            </Toolbar>
          </AppBar>
          
          <Container maxWidth="xl" sx={{ mt: 3, mb: 3 }}>
            <Routes>
              <Route path="/" element={
                <Box>
                  <ParameterControls parameters={parameters} onParametersChange={setParameters} />
                  <LiveMonitor parameters={parameters} />
                </Box>
              } />
              <Route path="/case-studies" element={<CaseStudies />} />
              <Route path="/case-studies-config" element={<CaseStudiesConfig />} />
            </Routes>
          </Container>
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App; 